<template>
	<view class="container">
		<ClassTitle tit="中职资讯" path="/pages/news/news" />
		<template v-if="newsData.length">
			<NewsList :newsData="newsListFn" />
		</template>
		<template v-else>
		<view class="news-item" v-for="n in SkeletonData">
			<view class="title"><HomeSkeleton width="100%" height="21px" bg="#e4e4e4"  animated /></view>
			<view class="list-box">
				<HomeSkeleton class="list-left" width="110px" height="76px" bg="#e4e4e4"  animated />
				<view class="list-right">
					<view class="list-smalltext">
						<HomeSkeleton width="100%" height="60px" bg="#e4e4e4"  animated />
					</view>
					<view class="info">
						<view class="info-left">
							<HomeSkeleton width="40px" height="14px" bg="#e4e4e4"  animated />
						</view>
						<view class="info-right">
							<HomeSkeleton width="120px" height="14px" bg="#e4e4e4"  animated />
						</view>
					</view>
				</view>
			</view>
		</view>
			
		</template>
		<ClassMore path="/pages/news/news" />

	</view>
</template>

<script>
	import ClassTitle from '../library/ClassTitle.vue'
	import NewsList from '../NewsList.vue'
	import {
		getNewsList
	} from '@/api/news.js'
	import {
		websiteUrl,
		classMap
	} from '@/utils/config.js'
	import ClassMore from '@/components/library/ClassMore.vue'
	import HomeSkeleton from '@/components/library/HomeSkeleton.vue'
	export default {
		name: 'HomeNews',
		components: {
			ClassTitle,
			ClassMore,
			NewsList,
			HomeSkeleton
		},
		data() {
			return {
				newsData: [],
				SkeletonData: [1,2,3,4]
			}
		},
		created() {
			getNewsList().then(res => {
				this.newsData = res.data.rows
			})
		},
		computed: {
			newsListFn() {
				return this.newsData.map(item => {
					return {
						title: item.title,
						titlepic: websiteUrl + item.titlepic,
						newstime: item.newstime,
						smalltext: item.smalltext,
						id: item.id,
					}
				})
			}
		},
	}
</script>

<style scoped lang="scss">
	.container {
		margin: 20px 0;
	}
</style>
<style lang="scss" scoped>
	.news-item {
		border-radius: 5px;
		background-color: #fff;
		padding: 13px 15px;
		margin-top: 8px;
		// box-shadow: 0 0px 4px rgba(0, 0, 0, 0.1);

		.title {
			font-size: 16px;
			font-weight: 700;
			color: #101010;
			margin-bottom: 8px;
		}

		.list-box {
			display: flex;

		}

		.list-left {
			flex-shrink: 0;
			width: 110px;
			height: auto;

			margin-right: 5px;
			border-radius: 3px;
		}

		.list-right {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			width: 100%;

			.list-smalltext {
				text-align: justify;
				font-size: 15px;
				color: #555;
				@include textEllipsis(3);
			}


			.info {
				display: flex;
				justify-content: space-between;
				font-size: 12px;

				.info-left {}

				.info-right {
					color: #a6a4a4;
				}
			}
		}

	}
</style>
